.board__row {
  display: flex;
  flex-direction: row;
}

.board__cell {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  width: 9vw;
  height: 9vw;
  cursor: pointer;
  user-select: none;
}

.board__cell--unknown {
  background-color: rgba(0, 255, 0, .25);
}

.board__cell--miss {
  background-color: rgba(0, 0, 0, .1);
}

.board__cell--damaged {
  background-color: rgba(255, 255, 0, .25);
}

.board__cell--dead {
  background-color: rgba(255, 0, 0, .25);
}

@media screen and (min-width: 550px) {
  .board__cell {
    width: 50px;
    height: 50px;
  }
}
